.suggestions{
    margin-top:30px ;
    margin-right: 20px;
}
.suggestions__title{
    color: gray;
    font-weight: bold;
}
.usern__left{
    display: flex;
    align-items: center;
}
.usern_info{
    display: flex;
    flex-direction: column;
}
.relation{
    color: gray;
    font-size: 15px;
}
.suggestion__usern{
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
}
.follow__button{
    color: #0095f6;
    font-weight: bold;
    background: 0;
    border: 0;
}
.follow__button:hover{
    color: white;
    cursor: pointer;
}
.username{
    font-weight: 600;
    font-size: 14px;
}
.avatar{
    margin-right: 10px;
}
