.post{
    width: 550px;
    margin: 38px 40px 50px 40px;
}
.posts_top{
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    margin-bottom: 10px;
}
posts_topProfile{
    display: flex;
    flex-direction: row;
    align-items: center;
    font-weight: bolder ;
}
.posts_topProfile > span{
    color: gray;
    font-size: 15px;
}
.posts_image > img{
    width: 100%;
    border-radius: 6px;
    border: 0.6px solid rgba(128, 128, 128, 0.516);
}
.post_bottomIcons{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.postIcon{
    padding: 7px;
    font-size: 30px;
}
.postIcon:hover{
    cursor: pointer;
}